.langs {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: center;
  flex-flow: row wrap;
  margin: 50px 0;
}

.langs .lang {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column wrap;
  margin: 3px 5px;
  width: 120px;
  height: 120px;
  box-shadow: 1px 1px 10px rgba(160, 160, 160, 0.699);
  background-color: #fff;
  border-radius: 25px;
}

.langs .lang span {
  color: rgb(29, 29, 29);
  font-size: 15px;
}

.langs .lang i {
  font-size: 55px;
  color: teal;
  transition: all 0.4s linear;
}

.langs .lang i:hover {
  transition: all 0.3s linear;
  color: rgb(0, 0, 0);
}

.about {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 20px;
  min-height: 550px;
  height: fit-content;
}

.about .left,
.about .right {
  flex: 1 0 350px;
}

.about .left h3,
.timelineBox .text h3 {
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
  text-shadow: 3px 3px 12px rgba(0, 68, 68, 0.432);
}

.about .left p {
  font-size: 17px;
  padding: 20px 100px;
  color: rgb(0, 0, 0);
}

.about .right {
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  background: linear-gradient(teal, rgb(1, 92, 92), rgb(1, 66, 66));
  min-height: 400px;
  max-width: 450px;
}

.about .right img {
  width: 100%;
  height: 80%;
}

.timelineBox {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  width: 75%;
  margin: 10px auto;
  padding: 10px;
  margin-top: 80px;
}

.timelineBox .text {
  font-size: 17px;
  color: rgb(0, 0, 0);
  margin-bottom: 80px;
}

.container {
  width: 95%;
  margin: 120px auto;
  box-shadow: 1px 1px 15px rgba(177, 177, 177, 0.452);
  padding: 20px;
}

.container h2 {
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
  text-shadow: 3px 3px 12px rgba(0, 68, 68, 0.432);
}

.container .progress {
  position: relative;
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
  align-items: center;
  width: 75%;
  margin: 0 auto;
  padding: 20px;
}

.container .progress .child::before {
  content: "";
  position: absolute;
  width: 100%;
  background-color: teal;
  height: 5px;
  left: 0;
  bottom: 0;
}

.container .progress .child {
  position: relative;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
  margin: 20px 20px;
  flex: 1 0 300px;
  box-shadow: 1px 1px 15px rgba(0, 59, 50, 0.432);
  border-radius: 5px;
  background-color: #fff;
}

.container .progress .child h3 {
  color: rgba(14, 14, 14, 0.932);
  font-size: 18px;
  text-shadow: 3px 3px 12px rgba(0, 68, 68, 0.432);
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
}

.container .progress .child:nth-child(1),
.container .progress .child:nth-child(3) {
  transform: translateY(-30px) scale(0.95);
  transition: all 0.5s linear;
}
.container .progress .child:nth-child(2),
.container .progress .child:nth-child(4) {
  transform: translateY(30px) scale(0.95);
  transition: all 0.5s linear;
}

.container .progress .child i {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  font-size: 200px;
  color: rgba(0, 80, 80, 0.12);
}

.num {
  position: absolute;
  font-size: 150px;
  color: rgba(131, 131, 131, 0.226);
  font-weight: 800;
}

.num2 {
  position: absolute;
  font-size: 150px;
  color: rgba(131, 131, 131, 0.226);
  font-weight: 800;
  right: 20px;
}

@media screen and (max-width: 1050px) {
  .langs .lang i {
    font-size: 35px;
    padding: 5px 12px;
    transition: all 0.7s linear;
  }

  .container .progress .child:nth-child(1),
  .container .progress .child:nth-child(3) {
    transform: translateY(0) scale(0.95);
    transition: all 0.5s linear;
  }
  .container .progress .child:nth-child(2),
  .container .progress .child:nth-child(4) {
    transform: translateY(0) scale(0.95);
    transition: all 0.5s linear;
  }
}

@media screen and (max-width: 650px) {
  .about .left,
  .about .right {
    width: 100%;
  }

  .langs .lang {
    margin: 5px;
    width: 100px;
    height: 100px;
  }

  .langs .lang span {
    font-size: 12px;
  }

  .num,
  .num2 {
    font-size: 100px;
  }
  .about .left p {
    padding: 20px;
    text-align: justify;
  }
}

@media screen and (max-width: 360px) {
  .about .right {
    max-width: 320px;
  }
  .num,
  .num2 {
    font-size: 70px;
  }

  .langs .lang {
    margin: 5px 10px;
    width: 100px;
    height: 100px;
  }

  .langs .lang span {
    font-size: 10px;
  }

  .langs .lang i {
    font-size: 30px;
  }
}

@media screen and (max-width: 480px) {
  .langs .lang {
    margin: 5px 3px;
    width: 80px;
    height: 80px;
  }

  .langs .lang i {
    font-size: 30px;
  }
}
