.photos {
  position: relative;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  padding: 10px 25px;
}

.photos img {
  margin: 10px 15px;
  width: 350px;
  flex: 0 0 350px;
  transition: all 0.5s linear;
  box-shadow: 2px 2px 15px rgba(0, 27, 23, 0.836);
  filter: contrast(1.2);
}

.photos img:hover {
  transform: translateY(-8px);
}

@media screen and (max-width: 360px) {
  .photos img {
    width: 320px;
    flex: 0 0 320px;
  }
}
