.projects {
  margin-top: 77px;
  position: relative;
  overflow: hidden;
}

.projects .img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  background: url(../../Media/projects.jpg) no-repeat;
  height: 90vh;
  max-width: 100%;
  background-size: cover;
  background-attachment: fixed;
  padding: 20px;
}

.projects .img::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 77, 77, 0.274);
}
