.about {
  margin-top: 77px;
  position: relative;
  overflow: hidden;
}

.about .img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  background: url(../../Media/about.jpg) no-repeat center;
  height: 100vh;
  max-width: 100%;
  background-size: cover;
  background-attachment: fixed;
  padding: 20px;
}

.about .img::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 77, 77, 0.274);
}

.about .img .text {
  color: #fff;
  z-index: 100;
}

.skills,
.reviews,
.cv {
  position: relative;
  width: 100%;
  padding: 30px 50px;
  margin: 80px auto;
}

.skills h2,
.reviews h2,
.cv h2 {
  position: relative;
  width: fit-content;
  margin: 20px 0;
  padding: 10px 0;
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
  text-shadow: 3px 3px 12px rgba(0, 68, 68, 0.432);
}

.cards {
  position: relative;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

.cards .card {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  flex: 1 0 300px;
  align-items: center;
  margin: 10px 15px;
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.75);
  padding: 15px;
  border-radius: 8px;
  transition: all 0.4s linear;
}

.cards .card:hover {
  transform: translateY(-15px);
  box-shadow: 1px 1px 15px rgba(0, 59, 50, 0.432);
  transition: all 0.4s linear;
}

.cards .card::before {
  content: "";
  position: absolute;
  width: 100%;
  background-color: teal;
  height: 5px;
  left: 0;
  bottom: 0;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.cards .card i:nth-of-type(1) {
  font-size: 80px;
  margin: 15px 0;
  color: teal;
}

.cards .card i:nth-of-type(2) {
  position: absolute;
  font-size: 250px;
  color: rgba(0, 128, 128, 0.09);
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  z-index: -100;
}

.cards .card h3 {
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
}

.cards .card p {
  color: rgba(0, 0, 0, 0.938);
  text-align: justify;
}

.num {
  position: absolute;
  font-size: 150px;
  color: rgba(131, 131, 131, 0.226);
  font-weight: 800;
  right: 20px;
}

.num2 {
  position: absolute;
  font-size: 150px;
  color: rgba(131, 131, 131, 0.226);
  font-weight: 800;
  left: 20px;
  bottom: -150px;
}

.reviews .cards {
  display: flex;
  position: relative;
  flex-flow: row wrap;
}

.cv {
  position: relative;
  padding: 20px;
}

@media screen and (max-width: 650px) {
  .num,
  .num2 {
    font-size: 100px;
  }
}

@media screen and (max-width: 360px) {
  .cards .card i:nth-of-type(2) {
    font-size: 200px;
  }

  .num,
  .num2 {
    font-size: 70px;
  }
}
