.wrapper {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  flex-flow: row wrap-reverse;
  justify-content: space-between;
}

.right {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  padding: 20px;
  width: 50%;
}

.right .input {
  margin: 10px;
}

.right .input input,
.right .input textarea {
  padding: 7px;
  padding-left: 12px;
  outline: none;
  width: 95%;
  border: none;
  border-bottom: 2px solid rgba(92, 92, 92, 0.384);
  resize: none;
  border-radius: 3px;
  transition: all 0.5s linear;
}

.right .input input:focus,
.right .input textarea:focus {
  border-bottom: 2px solid teal;
  transition: all 0.5s linear;
}

.right button {
  font-size: 17px;
  padding: 13px 15px;
  outline: none;
  width: 200px;
  border: none;
  resize: none;
  border-radius: 3px;
  margin: 5px 10px;
  background-color: teal;
  color: white;
  box-shadow: 1px 1px 15px rgba(0, 73, 73, 0.342),
    -1px -1px 15px rgba(1, 68, 68, 0.425);
  transition: all 0.3s linear;
}

.right button:hover {
  background: rgb(0, 90, 90);
  color: rgb(255, 255, 255);
  transition: all 0.3s linear;
}

.wrapper .left {
  position: relative;
  width: 50%;
  height: 439px;
}

.wrapper .left img {
  width: 100%;
  height: 439px;
}

@media screen and (max-width: 850px) {
  .wrapper .right {
    width: 100%;
  }

  .wrapper .left {
    width: 100%;
  }
}

@media screen and (max-width: 450px) {
  .right button {
    width: 150px;
    padding: 10px 12px;
    font-size: 15px;
  }
}
