.text {
  position: relative;
  width: 100%;
  display: block;
  padding: 15px 20px;
  backdrop-filter: blur(16px) saturate(200%);
  -webkit-backdrop-filter: blur(16px) saturate(200%);
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
}

.text h1 {
  font-size: 50px;
  font-weight: 600;
}

.text span:nth-of-type(1) {
  font-size: 50px;
  margin-right: 10px;
}

.text span {
  font-size: 30px;
  color: rgb(255, 255, 255);
  font-weight: 600;
  font-family: "Electrolize", sans-serif;
}

@media screen and (max-width: 850px) {
  .text h1 {
    font-size: 30px;
  }

  .text span:nth-of-type(1) {
    font-size: 30px;
  }

  .text span {
    font-size: 20px;
  }
}

@media screen and (max-width: 360px) {
  .text h1 {
    font-size: 25px;
  }

  .text span:nth-of-type(1) {
    font-size: 25px;
  }

  .text span {
    font-size: 15px;
  }
}
