.footer {
  position: relative;
  margin: 0 auto;
  margin-top: 80px;
  padding: 20px 40px;
  display: flex;
  flex-flow: row wrap;
  min-height: 250px;
}

.footer .left,
.footer .middle,
.footer .right {
  flex: 1 0 300px;
  margin: 10px 15px;
}

.footer .middle,
.footer .left {
  align-self: center;
}

.logo {
  font-size: 25px;
  font-family: "Pacifico", cursive;
}

.footer .middle i {
  font-size: 18px;
  margin-right: 10px;
}

.footer .right {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.footer .right iframe {
  width: 100%;
  height: 300px;
  flex: 1 0 300px;
  margin: 5px auto;
  margin-bottom: 20px;
}

.copyright {
  position: absolute;
  color: rgba(129, 129, 129, 0.877);
  font-size: 15px;
  transform: translateX(-50%);
  left: 50%;
  bottom: 5px;
  text-align: center;
  width: 100%;
}

@media screen and (max-width: 850px) {
  .footer {
    padding: 10px;
  }

  .copyright {
    font-size: 15px;
  }
}

@media screen and (max-width: 450px) {
  .copyright {
    font-size: 12px;
  }
}
