.card {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  margin: 10px 15px;
  background-color: rgb(255, 255, 255);
  flex: 0 0 380px;
  box-shadow: 1px 1px 15px rgba(0, 59, 50, 0.432);
  transition: all 0.5s linear;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-10px);
  transition: all 0.3s linear;
}

.card:hover .right i {
  transform: rotate(306deg);
  transition: all 0.3s linear;
}

.card .right,
.card .left {
  margin-left: 15px;
}

.card .left {
  flex: 1;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  min-width: 100px;
}

.card .left span {
  color: rgba(59, 59, 59, 0.822);
  font-family: "Electrolize", sans-serif;
  font-weight: 600;
}

.card .right {
  position: relative;
  color: #ddd;
  padding: 10px;
  flex: 3;
  flex-flow: column wrap;
  background-color: teal;
  height: 100%;
}

.card .right i {
  position: absolute;
  font-size: 120px;
  right: 5px;
  bottom: 10px;
  color: rgba(221, 221, 221, 0.283);
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  transition: all 0.3s linear;
}

.card img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

@media screen and (max-width: 360px) {
  .card {
    flex: 1 0 330px;
  }
}
