.cards {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding: 25px 35px;
}

@media screen and (max-width: 360px) {
  .cards {
    justify-content: center;
  }
}

@media screen and (max-width: 1200px) {
  .cards {
    justify-content: center;
  }
}
