.modal {
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 3, 3, 0.774);
  z-index: 100000;
  justify-content: center;
  align-items: center;
}

.box {
  display: flex;
  flex-flow: column wrap;
  position: relative;
  width: auto;
  background-color: #fff;
  box-shadow: 2px 2px 35px rgba(0, 31, 26, 0.568);
  width: 320px;
  transition: all 1.2s linear;
}

.box i {
  position: absolute;
  right: 12px;
  top: 12px;
  font-size: 20px;
  cursor: pointer;
}

.box i:hover {
  color: #fff;
  transition: all 0.2s linear;
}

.box img {
  width: auto;
  height: 420px;
}

.box span {
  background-color: teal;
  padding: 10px;
  color: #fff;
  font-weight: lighter;
  font-size: 17px;
}
