.card {
  position: relative;
  flex: 0 0 380px;
  display: flex;
  flex-flow: column wrap;
  margin: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 15px rgba(0, 59, 50, 0.432);
  background-color: #fff;
  border-radius: 10px;
}

.card .img {
  height: auto;
  width: 100%;
  filter: grayscale(10);
  transition: all 0.5s linear;
  cursor: pointer;
  padding: 20px;
}

.card .img img {
  width: 100%;
  height: 200px;
  margin: auto;
}

.card .text {
  padding: 10px 20px;
}

.card:hover .img {
  filter: grayscale(0);
  transition: all 0.5s linear;
  transform: scale(1.08);
}

.card span {
  font-weight: 600;
}

.card .desc {
  margin: 12px 0;
  display: flex;
  flex-flow: row wrap;
}

.card .desc span {
  background-color: rgba(0, 61, 61, 0.178);
  margin-right: 5px;
  margin-top: 5px;
  padding: 7px 15px;
  border-radius: 25px;
  font-weight: normal;
  font-size: 13px;
}

.card button {
  font-size: 17px;
  padding: 13px 20px;
  outline: none;
  border: none;
  resize: none;
  border-radius: 3px;
  background-color: teal;
  color: white;
  box-shadow: 1px 1px 15px rgba(0, 73, 73, 0.342),
    -1px -1px 15px rgba(1, 68, 68, 0.425);
  transition: all 0.3s linear;
  width: 100%;
  align-self: flex-end;
}

.card button:hover {
  background: rgb(0, 90, 90);
  color: rgb(255, 255, 255);
  transition: all 0.3s linear;
}

@media screen and (max-width: 360px) {
  .card {
    flex: 1 0 320px;
  }
}
