.video {
  position: relative;
  display: flex;
  width: 100%;
  height: auto;
  margin-top: 70px;
}

.video::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.568);
}

.video video {
  width: 100%;
  height: auto;
}

.box {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  padding: 15px 25px;
  backdrop-filter: blur(16px) saturate(140%);
  -webkit-backdrop-filter: blur(16px) saturate(150%);
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
}

.box span {
  font-size: 27px;
  color: #fff;
  font-weight: 700;
  font-family: "Electrolize", sans-serif;
  transition: all 0.7s linear;
}

.box h1 {
  font-size: 100px;
  color: #fff;
  font-weight: 800;
  font-family: "Electrolize", sans-serif;
  text-shadow: 1px 1px 10px #fff;
  animation: textneon 2s linear 1.5s infinite alternate;
  transition: all 0.7s linear;
}

@keyframes textneon {
  0% {
    text-shadow: none;
  }
  50% {
    text-shadow: 1px 1px 15px rgb(146, 248, 248);
  }
  100% {
    text-shadow: none;
  }
}

@media screen and (max-width: 850px) {
  .box h1 {
    font-size: 55px;
    transition: all 0.7s linear;
  }

  .box span {
    font-size: 22px;
  }
}

@media screen and (max-width: 360px) {
  .box h1 {
    font-size: 40px;
    transition: all 0.7s linear;
  }

  .box span {
    font-size: 20px;
  }
}
