.box {
  position: fixed;
  left: 0;
  top: calc(50% - 70px);
  display: flex;
  align-items: center;
  flex-flow: column wrap;
  height: fit-content;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 1000;
}

.box i {
  padding: 15px 17px;
  font-size: 17px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s linear;
  color: rgb(0, 0, 0);
}

.box i:not(:nth-child(4)) {
  border-bottom: 1px solid #ddd;
}

.box i:hover {
  background-color: teal;
  transition: all 0.3s linear;
}

@media screen and (max-width: 850px) {
  .box {
    position: fixed;
    transform: translateX(-50%);
    left: 50%;
    top: calc(100% - 45px);
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    background-color: rgba(255, 255, 255, 0.9);
    z-index: 1000;
    width: fit-content;
  }

  .box i:not(:nth-child(4)) {
    border-right: 1px solid #ddd;
  }
}

@media screen and (max-width: 360px) {
  .box {
    position: fixed;
    transform: translateX(-50%);
    left: 50%;
    top: calc(100% - 45px);
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    background-color: rgba(255, 255, 255, 0.9);
    z-index: 1000;
    width: fit-content;
  }

  .box i:not(:nth-child(4)) {
    border-right: 1px solid #ddd;
  }
}
